<template>
  <div ref="HouseEchartsRel" style="height: 350px;width: 390px">
  </div>
</template>

<script>
import * as Echarts from "echarts";

export default {
  name: "HouseEcharts",
  data() {
    return {
      Echarts: null,
    }
  },
  mounted() {

    //发送请求
    const data = [];
    this.$axios.get('/bigData/selectCountHouse').then(res => {
      res.data.data.forEach(i => {
        data.push(i.Sum)
      })
      this.init(data);
    })

  },
  methods: {
    //1.初始化图表
    init(data) {
      this.Echarts = Echarts.init(this.$refs.HouseEchartsRel)
      //2.配置数据
      let option = {
        title: {
          text: "社区住户",
          align: "center",
          color: "#ffffff",
          fontSize: 20,
          top: "3%",
          left: "10%",
        },
        backgroundColor: "#ffffff",
        grid: {
          top: "25%",
          bottom: "10%", //也可设置left和right设置距离来控制图表的大小
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "shadow",
            label: {
              show: true,
            },
          },
        },
        legend: {
          data: ["每栋住户"],
          top: "15%",
          color: "#ffffff",
        },
        xAxis: {
          data: ["A栋", "B栋", "C栋", "D栋", "E栋"],
          axisLine: {
            show: true, //隐藏X轴轴线
            lineStyle: {
              color: "#01FCE3",
            },
          },
          axisTick: {
            show: true, //隐藏X轴刻度
          },
          axisLabel: {
            show: true,
            color: "#000000", //X轴文字颜色
          },
        },
        yAxis: [
          {
            type: "value",
            name: "户",
            nameTextStyle: {
              color: "#000000",
            },
            splitLine: {
              show: false,
            },
            axisTick: {
              show: true,
            },
            axisLine: {
              show: true,
              lineStyle: {
                color: "#000000",
              },
            },
            axisLabel: {
              show: true,
              color: "#000000",
            },
          },
        ],
        series: [
          {
            name: "每栋住户",
            type: "bar",
            barWidth: 40,
            itemStyle: {
              color: new Echarts.graphic.LinearGradient(0, 0, 0, 1, [
                {
                  offset: 0,
                  color: "#00FFE3",
                },
                {
                  offset: 1,
                  color: "#4693EC",
                },
              ]),
            },
            data: data,
          },
        ],
      };
      // 3.传入数据绘制图表
      this.Echarts.setOption(option);
    }
  }
}
</script>

