<template>
  <div ref="CircularRels" style="height: 380px"></div>
</template>
<script>
import * as Echarts from "echarts";

export default {
  name: "CircularEcharts",
  data() {
    return {
      CircularEcharts: null,
    }
  },
  created() {
    const data1 = [];
    this.$axios.get('/getAllGoods').then(res => {
      const resp = res.data.data;
      const title = ['口罩', '防护服', '消毒液', '手套', '体温计'];
      for (let i = 0; i < resp.length; i++) {
        const data = {};
        data.value = resp[i].count;
        data.name = resp[i].title;
        data1.push(data)
      }
      this.init(data1);
    })
  },
  mounted() {
    window.addEventListener('resize', () => {
      this.CircularEcharts.resize();
    });
  },
  methods: {
    //1.初始化图表
    init(data1) {
      if (!this.$refs.CircularRels) return
      this.CircularEcharts = Echarts.init(this.$refs.CircularRels)
      //2.配置数据
      let option = {
        title: {
          text: '物资储备',
          subtext: 'Fake Data',
          left: 'left'
        },
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b} : {c} ({d}%)'
        },
        legend: {
          orient: 'horizontal',
          padding: [320, 10, 100, 10],
        },
        toolbox: {
          show: true,
          feature: {
            mark: {show: true},
            dataView: {show: true, readOnly: false},
            restore: {show: true},
            saveAsImage: {show: true}
          }
        },
        series: [
          {
            name: '名称',
            type: 'pie',
            radius: [15, 130],
            center: ['50%', '45%'],
            roseType: 'area',
            labelLine: {
              length: 1,
              length2: 10
            },
            itemStyle: {
              borderRadius: 10
            },
            data: data1
          },
        ]
      };
      // 3.传入数据绘制图表
      this.CircularEcharts.setOption(option);
    }
  },
}
</script>
