import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'
import Welcome from '@/views/Welcome'
import Admin from '../views/user/Admin.vue'

Vue.use(VueRouter)

//解决冗余导航的报错------------------------------
const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
    return originalPush.call(this, location).catch(err => err)
}
//----------------------------------------------
const routes = [

    {
        path: '/',
        //重定向
        redirect: '/login',
    },
    {
        path: '/login',
        name: 'Login',
        component: () => import('../views/Login.vue')
    },
    {
        path: '/home',
        name: 'Home',
        //重定向
        redirect: '/welcome',
        component: HomeView,
        children: [
            {
                path: '/echarts',
                name: 'Echarts',
                component: () => import('../components/echarts/HouseEcharts.vue')
            },
            {
                path: '/test',
                name: 'Test',
                component: () => import('@/views/Test.vue')
            },
            {
                path: '/welcome',
                name: 'Welcome',
                component: Welcome,
                // component:()=>import('@/views/Welcome.vue')
            },
            {
                path: '/users',
                name: 'User',
                component: () => import('@/views/user/User.vue')
            },
            {
                path: '/admin',
                name: 'Admin',
                component: Admin,
                // component:()=>import('@/components/user/Admin.vue')
            },
            {
                path: '/register',
                name: 'Register',
                component: () => import('../views/register/Register.vue'),
            },
            {
                path: '/leave',
                name: 'Leave',
                component: () => import('@/views/leave/Leave.vue')
            },
            {
                path: '/leaveApplications',
                name: 'LeaveApplications',
                component: () => import('@/views/leave/LeaveApplications.vue')
            },
            {
                path: '/return',
                name: 'Return',
                component: () => import('@/views/return/Return.vue')
            },
            {
                path: '/returnApplication',
                name: 'ReturnApplication',
                component: () => import('@/views/return/ReturnApplication.vue')
            },
            {
                path: '/personalcode',
                name: 'PersonalCode',
                component: () => import('@/views/personcode/PersonalCode.vue'),
            },
            {
                path: '/divide',
                name: 'Divide',
                component: () => import('@/views/divide/Divide.vue'),
            },
            {
                path: '/divideHistory',
                name: 'DivideHistory',
                component: () => import('@/views/divide/DivideHistory.vue'),
            },
            {
                path: '/hodometer',
                name: 'HodoMeter',
                component: () => import('@/views/hodometer/HodoMeter.vue'),
            },
            {
                path: '/goods',
                name: 'Goods',
                component: () => import('@/views/goods/Goods.vue')
            },
            {
                path: '/info',
                name: 'Info',
                component: () => import('@/views/info/Info.vue')
            },
        ]
    },
]
const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes
})

//路由导航守卫
router.beforeEach((to, form, next) => {

    if (to.path == '/login') {      //如果是调到登录界面则不阻拦
        next();
    } else if (to.path == '/public/map/index.html') {
        next();
    } else {
        //得到token
        const token = window.sessionStorage.getItem('token');
        if (!token) {
            next('/login');   //如果没有token就跳到登录界面
        } else {
            next();     //如果有token就不阻拦
        }
    }
})

export default router
