<template>
  <el-row :gutter="10" class="home">
    <el-col :span="8">
      <el-card shadow="hover">
        <div class="user">
          <img :src="userIcon">
          <div class="userinfo">
            <p class="name">{{ this.user.username }}</p>
            <p class="access">超级管理员</p>
          </div>
        </div>
        <div class="login-info">
          <p>登录地点：重庆·合川</p>
          <p>现在时间：{{ dateFormat(newDate) }}</p>
        </div>
      </el-card>
      <el-card style="margin-top: 10px; height: 520px">
        <el-table :data="tableList">
          <el-table-column
              label="名称"
              prop="name">
          </el-table-column>
          <el-table-column
              label="已用数量/只"
              prop="count">
          </el-table-column>
          <el-table-column
              label="剩余数量/只"
              prop="delCount">
          </el-table-column>
        </el-table>
      </el-card>
    </el-col>
    <el-col :span="16">
      <el-card style="height: 180px">
        <!--横向柱形图-->
        <DivideEcharts></DivideEcharts>
      </el-card>
      <el-card style="margin-top: 5px; height: 230px">
        <!--折线图-->
        <PassEcharts></PassEcharts>
      </el-card>
      <div class="graph">
        <!--竖向柱状图-->
        <el-card style="height: 375px">
          <!--          <HouseholdEcharts></HouseholdEcharts>-->
          <HouseEcharts></HouseEcharts>
        </el-card>
        <!--扇形图-->
        <el-card style="height: 375px">
          <CircularEcharts></CircularEcharts>
        </el-card>
      </div>
    </el-col>
  </el-row>
</template>
<script>
//解构api中axios封装的方法
//引入自己写的echarts组件
import HouseholdEcharts from '../components/echarts/HouseholdEcharts'
import PassEcharts from "@/components/echarts/PassEcharts"
import CircularEcharts from "@/components/echarts/CircularEcharts";
import DivideEcharts from "@/components/echarts/Divide";
import HouseEcharts from "@/components/echarts/HouseEcharts"

export default {
  name: 'Welcome',
  data() {
    return {
      newDate: new Date(),
      userIcon: require("@/assets/avator.jpg"),
      tableList: [],
      user: null,
    }
  },
  components: {
    PassEcharts,
    HouseholdEcharts,
    CircularEcharts,
    DivideEcharts,
    HouseEcharts
  },
  created() {
    this.user = this.$store.state.user.UserInfo;
  },
  mounted() {
    const tableList = [];
    this.$axios.get('/getAllGoods').then(res => {
      const resp = res.data.data;
      for (let i = 0; i < resp.length; i++) {
        const data = {};
        data.name = resp[i].title;
        data.count = resp[i].count;
        data.delCount = resp[i].delCount;
        tableList.push(data)
      }
      this.tableList = tableList;
    })
    let _this = this; // 声明一个变量指向Vue实例this，保证作用域一致
    _this.timer = setInterval(() => {
      _this.newDate = new Date();//修改数据date
    }, 1000)
  },
  methods: {
    //时间格式化
    dateFormat() {
      var date = new Date();
      var year = date.getFullYear();
      /*
       * 在日期格式中，月份是从0开始的
       * 使用三元表达式在小于10的前面加0，以达到格式统一  如 09:11:05
       */
      var month = date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1
      var day = date.getDate() < 10 ? '0' + date.getDate() : date.getDate()
      var hours = date.getHours() < 10 ? '0' + date.getHours() : date.getHours()
      var minutes = date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes()
      var seconds = date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds()
      let week = date.getDay() // 星期
      let weekArr = ['星期日', '星期一', '星期二', '星期三', '星期四', '星期五', '星期六']
      // 拼接 时间格式处理
      return year + '年' + month + '月' + day + '日 ' + hours + ':' + minutes + ':' + seconds + ' ' + weekArr[week]
    }
  },
  beforeDestroy() {
    if (this.timer) {
      clearInterval(this.timer); // 在Vue实例销毁前，清除我们的定时器
    }
  }
}
</script>
