<template>
  <div ref="DivideReals" style="height: 210px"></div>
</template>
<script>
import * as Echarts from "echarts";

export default {
  name: "Divide",
  data() {
    return {
      DivideEcharts: null
    }
  },
  created() {
    this.$axios.get('/bigData/selectDivide').then(res => {
      const array = res.data.data;
      const xData = Object.values(array);
      this.init(xData);
    })
  },
  mounted() {
    window.addEventListener('resize', () => {
      this.DivideEcharts.resize();
    });
  },
  methods: {
    //1.初始化图表
    init(xData) {
      if (!this.$refs.DivideReals) return
      this.DivideEcharts = Echarts.init(this.$refs.DivideReals)
      //2.配置数据
      let option = {
        title: {
          text: '隔离人员统计:' + xData[4]
        },
        tooltip: {},
        legend: {
          data: ['数量']
        },
        xAxis: {},
        yAxis: {
          type: "category",
          data: ["咳嗽", "感冒", "发烧", "其他"]
        },
        series: [{
          name: '数量',
          type: 'bar',
          data: xData
        }]
      };
      // 3.传入数据绘制图表
      this.DivideEcharts.setOption(option);
    }
  },
}
</script>

