<script>
export default {
  name: 'HomeView',
  data() {
    //验证邮箱的规则
    const checkEmail = (rule, value, cb) => {
      const regEmail = /^\w+@\w+(\.\w+)+$/
      if (regEmail.test(value)) {
        return cb()
      }
      //返回一个错误提示
      cb(new Error('请输入合法的邮箱'))
    };
    //验证手机号码的规则
    const checkMobile = (rule, value, cb) => {
      const regMobile = /^1[345789]\d{9}$/
      if (regMobile.test(value)) {
        return cb()
      }
      //返回一个错误提示
      cb(new Error('请输入合法的手机号码'))
    };
    return {
      imageUrl: '',
      isCollapse: false,
      editVisible: false,
      iconVisible: false,
      user: {},
      imgUrl: '',
      imgUpUrl: this.$store.state.ip.ImgUpLoad,
      path: '',
      fataData: '',
      //修改用户对话框验证规则
      editFormRules: {
        mobile: [
          {validator: checkMobile, trigger: 'blur'}
        ],
        email: [
          {validator: checkEmail, trigger: 'blur'}
        ],
      },
    }
  },
  computed: {
    getImgUrl() {
      return this.$store.state.imgUrl
    },
  },
  mounted() {
    this.user = this.$store.state.user.UserInfo;
    this.$axios.get('/icon/queryIcon?userId=' + this.user.userId).then(res => {
      const result = res;
      if (result.data.status !== 10000) {
        this.imgUrl = require("@/assets/avator.jpg")
      } else {
        this.imgUrl = result.data.data.iconUrl;
      }
    })
  },
  methods: {
    loginOut() {
      //退出则清除token
      // window.localStorage.removeItem('token')

      this.$store.commit("REMOVE_INFO");
      window.sessionStorage.removeItem('token');
      // window.sessionStorage.removeItem('userInfo');
      window.sessionStorage.removeItem('UserInfo');

      this.$axios.post('/logout').then(res => {
        // console.log(res)
        // const token = window.sessionStorage.getItem('token');
        // console.log(token)
      })
      //跳转到登录页面
      this.$router.push('/')
    },
    changIcon() {
      this.iconVisible = true;
    },
    //要上传的图片，会自动传过来
    handleelchange(file, fileList) {
      let formdata = new FormData()
      fileList.map(item => { //fileList本来就是数组，就不用转为真数组了
        formdata.append("file", item.raw)  //将每一个文件图片都加进formdata
      })
      this.fataData = formdata;
    },
    //上传头像
    uploadIcon() {
      console.log(this.fataData, '上传的fatadata')
      this.$axios.post('/icon//oosFile/upload', this.fataData).then(res => {
        if (res.data.status === 200) {
          const path = res.data.data
          console.log(path, '返回的path')
          this.$axios.post('/icon/add', {userId: this.user.userId, iconUrl: path}).then(res => {
            if (res.data.status === 10000) {
              this.$message.success(res.data.msg);
            } else {
              this.$message.error("添加失败")
            }
          })
        }
      })

      this.fataData = '';
      this.iconVisible = false;
    },
    closeIconDialog() {
      this.iconVisible = false;
    },
    editUser() {
      this.editVisible = !this.editVisible;
    },
    updateUser() {
      this.$refs["editFormRef"].validate((valid) => {
        if (!valid) {
          return alert("请输入正确的信息！")
        } else {
          // console.log(this.user)
          this.$axios.post('/admin/user/update', this.user).then(res => {
            if (res.data.status === 10000) {
              this.$message.success("修改管理员信息成功！");
              // console.log(this.user)
              this.editVisible = !this.editVisible;
              // console.log(this.user)
              this.$axios.get('/user/userDetail?userId=' + this.user.userId).then(res => {
                this.user = res.data.data;
                console.log(this.user,11111111)
              })
            } else {
              this.$message.error("修改管理员信息失败！");
              this.editVisible = !this.editVisible;
            }
          })
        }
      })
    },
    closeEditDialog() {
      //重置表单
      this.$refs["editFormRef"].resetFields();
      this.formData = [];
      // console.log(this.user)
    },
  },
}

</script>
