const user = {
    state: {
        UserName: "",  //用户名
        Status: "",      //用户状态
        UserInfo: {       // 用户信息
        },
        imgUrl: "",
        isActivity: "",
    },
    mutations: {
        setUserName(state, UserName) {
            state.UserName = UserName;
        },
        setStatus(state, Status) {
            state.Status = Status;
        },
        setUserInfo(state, UserInfo) {
            state.UserInfo = UserInfo;
        },
        setImgUrl(state, imgUrl) {
            state.imgUrl = imgUrl;
        },
        setIsActivity(state, isActivity) {
            state.isActivity = isActivity;
        }
    },
    getters: {
        getUserName: state => {
            return state.UserName;
        },
        getStatus: state => {
            return state.Status;
        },
        getUserInfo: state => {
            return state.UserInfo;
        },
        getImgUrl: state => {
            return state.imgUrl;
        },
    }
}
export default user
