<template>
  <div ref="PassRel" style="height: 250px"></div>
</template>

<script>
import * as Echarts from "echarts";

export default {
  name: "PassEcharts",

  data() {
    return {
      passEcharts: null,
      leaveData: [],
      returnData: [],
      date: [],
    }
  },
  created() {
    const date = [];
    const leaveData = [];
    const returnData = [];

    this.$axios.get('/bigData/selectByDayReturnAll').then(res => {
      if (res.data.status !== 10000) {
        this.$message.error("获取进出人流量失败")
      }
      const data = res.data.data;
      data.forEach(i => {
        returnData.push(i.count);
        date.push(i.date)
      })

      this.$axios.get('/bigData/selectByDayLeaveAll').then(res => {
        const data = res.data.data;
        data.forEach(i => {
          leaveData.push(i.count);
        })
        this.init(date, returnData, leaveData);
      })
    });

  },
  mounted() {
    window.addEventListener('resize', () => {
      this.passEcharts.resize();
    });
  },
  methods: {
    //1.初始化图表
    init(date, returnData, leaveData) {
      if (!this.$refs.PassRel) return
      this.passEcharts = Echarts.init(this.$refs.PassRel);
      //  2.配置数据
      let option = {
        title: {
          text: '社区近两周人口流量'
        },
        tooltip: {},
        legend: {
          data: ['离开人数', '进入人数']
        },
        xAxis: {
          type: 'category',
          data: date
        },
        yAxis: {
          type: 'value'
        },
        series: [
          {
            name: '离开人数',
            data: leaveData,
            type: 'line'
          },
          {
            name: '进入人数',
            data: returnData,
            type: 'line'
          }
        ]
      };
      // 4.传入数据绘制图表
      this.passEcharts.setOption(option);
    }
  }
}
</script>


