<template>
  <div>
    <!--面包屑导航-->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>系统管理</el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/admin' }">管理员列表</el-breadcrumb-item>
    </el-breadcrumb>

    <el-card>
      <!--搜索头部input框-->
      <el-row :gutter="15">
        <el-col :span="8">
          <el-input v-model="queryInfo.username" class="input-with-select" clearable placeholder="请输入内容"
                    @clear="getAdminList">
            <el-button slot="append" icon="el-icon-search" @click="getAdminList"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="addAdminVisible=true">添加管理员</el-button>
        </el-col>
      </el-row>
      <el-table
          :data="adminList"
          border
          :row-style="rowStyle"
          :cell-style="rowClass"
          :header-cell-style="headClass"
          style="width: 100%">
        <el-table-column
            label="序号"
            type="index"
            width="180">
        </el-table-column>
        <el-table-column
            label="姓名"
            prop="username"
            width="180">
        </el-table-column>
        <el-table-column
            label="电话"
            prop="mobile">
        </el-table-column>
        <el-table-column
            label="邮箱"
            prop="email">
        </el-table-column>
        <el-table-column
            label="角色"
            prop="status"
            width="160px">
          <template slot-scope="scope">
            <el-switch
                v-model=scope.row.status
                :active-value=2
                :inactive-value=1
                active-text="管理员"
                inactive-text="用户"
                @change="changeStatus(scope.row)"
            >
            </el-switch>
          </template>
        </el-table-column>
        <el-table-column
            label="负责区域"
            prop="address">
        </el-table-column>
        <el-table-column
            label="创建时间"
            prop="createTime">
        </el-table-column>
        <el-table-column
            label="操作"
            prop="salt">
          <template slot-scope="scope">
            <el-tooltip class="item" content="编辑" effect="dark" placement="top-start">
              <el-button circle icon="el-icon-edit" size="mini" type="primary"
                         @click="editAdmin(scope.row)"></el-button>
            </el-tooltip>
            <el-tooltip class="item" content="删除" effect="dark" placement="top-start">
              <el-button circle icon="el-icon-delete" size="mini" type="danger"
                         @click="removeAdminItem(scope.row.userId)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!--分页功能-->
      <el-pagination
          :current-page="queryInfo.pageNum"
          :page-size="queryInfo.pageSize"
          :page-sizes="[6,8,11,12]"
          :total="total"
          layout="total, sizes, prev, pager, next, jumper"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange">
      </el-pagination>
    </el-card>
    <!--添加管理员dialog对话框-->
    <el-dialog
        :visible.sync="addAdminVisible"
        title="添加管理员"
        width="35%"
        @close="closeAddAdminDialog"
    >
      <!--这是内容区域-->
      <el-form ref="addAdminFormRef" :model="admin" :rules="addAdminFormRules" class="demo-user" label-width="100px">
        <el-form-item label="管理员名" prop="username">
          <el-input v-model="admin.username"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input v-model="admin.password" autocomplete="off" type="password"></el-input>
        </el-form-item>
        <el-form-item label="邮箱" prop="email">
          <el-input v-model="admin.email"></el-input>
        </el-form-item>
        <el-form-item label="手机号码" prop="mobile">
          <el-input v-model="admin.mobile"></el-input>
        </el-form-item>
        <el-form-item label="角色" prop="status">
          <el-radio-group v-model="admin.status">
            <el-radio :label="0">禁用</el-radio>
            <el-radio :label="1">用户</el-radio>
            <el-radio :label="2">管理员</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="负责区域" prop="address">
          <el-input v-model="admin.address"></el-input>
        </el-form-item>
        <el-form-item label="选择时间" prop="createTime">
          <el-date-picker
              placeholder="选择日期时间："
              v-model="admin.createTime"
              style="float: left"
              size="large"
              type="datetime"
              value-format="yyyy-MM-dd HH:mm:ss">
          </el-date-picker>
        </el-form-item>
      </el-form>
    <el-button @click="addAdminVisible = false">取 消</el-button>
    <el-button type="primary" @click="addAdmin">确 定</el-button>
    </el-dialog>
    <!--修改用户dialog对话框-->
    <el-dialog
        :visible.sync=editAdminVisible
        title="修改管理员"
        width="35%"
        @close="closeEditAdminDialog"
    >
      <!--这是内容区域-->
      <el-form ref="editAdminFormRef" :model="admin" :rules="editAdminFormRules" class="demo-user" label-width="100px">
        <el-form-item label="用户名">
          <el-input v-model="admin.username" disabled></el-input>
        </el-form-item>
        <el-form-item disabled label="手机号码" prop="mobile">
          <el-input v-model="admin.mobile"></el-input>
        </el-form-item>
        <el-form-item label="邮箱" prop="email">
          <el-input v-model="admin.email"></el-input>
        </el-form-item>
        <el-form-item label="角色" prop="status">
          <el-input v-model="admin.status"></el-input>
        </el-form-item>
        <el-form-item label="负责区域" prop="address">
          <el-input v-model="admin.address"></el-input>
        </el-form-item>
        <el-form-item label="选择时间" prop="createTime">
          <el-date-picker
              style="float: left"
              v-model="admin.createTime"
              placeholder="选择日期时间"
              size="large"
              type="datetime"
              value-format="yyyy-MM-dd HH:mm:ss">
          </el-date-picker>
        </el-form-item>
      </el-form>
    <el-button @click="editAdminVisible = false">取 消</el-button>
    <el-button type="primary" @click="updateAdmin">确 定</el-button>
    </el-dialog>
  </div>
</template>

<script>

export default {
  name: "User",
  data() {

    //验证邮箱的规则
    const checkEmail = (rule, value, cb) => {
      const regEmail = /^\w+@\w+(\.\w+)+$/
      if (regEmail.test(value)) {
        return cb()
      }
      //返回一个错误提示
      cb(new Error('请输入合法的邮箱'))
    };
    //验证手机号码的规则
    const checkMobile = (rule, value, cb) => {
      const regMobile = /^1[345789]\d{9}$/
      if (regMobile.test(value)) {
        return cb()
      }
      //返回一个错误提示
      cb(new Error('请输入合法的手机号码'))
    };
    return {
      //  请求管理员列表的参数
      queryInfo: {
        username: '',
        pageNum: 1,
        pageSize: 6,
      },
      //管理员列表数据
      adminList: [],
      //当前数据总数
      total: 0,
      //修改或修改用户的信息
      admin: {
        userId: '',
        username: 'admin',
        password: '987654321',
        email: '246435343@qq.com',
        mobile: '13855556666',
        status: 2,
        createUserId: '123456',
        createTime: ''
      },
      //添加管理员对话框是否显示
      addAdminVisible: false,
      //添加管理员对话框验证规则
      addAdminFormRules: {
        username: [
          {required: true, message: '请输入管理员名', trigger: 'blur'},
          {min: 2, max: 6, message: '长度在 3 到 5 个字符', trigger: 'blur'}
        ],
        password: [
          {required: true, message: '请输入密码', trigger: 'blur'},
          {min: 6, max: 10, message: '长度在 3 到 5 个字符', trigger: 'blur'}
        ],
        mobile: [
          {validator: checkMobile, trigger: 'blur'}
        ],
        email: [
          {validator: checkEmail, trigger: 'blur'}
        ],
        status: [
          {required: true, message: '请输入当前角色', trigger: 'blur'},
        ],
        createUserId: [
          {required: true, message: '请输入创建者Id', trigger: 'blur'},
          {min: 3, max: 9, message: '长度在 3 到 9 个字符', trigger: 'blur'}
        ],
      },
      //控制修改管理员对话框是否显示
      editAdminVisible: false,
      //修改用户对话框验证规则
      editAdminFormRules: {
        mobile: [
          {validator: checkMobile, trigger: 'blur'}
        ],
        email: [
          {validator: checkEmail, trigger: 'blur'}
        ],
      },
    }
  },
  methods: {
    rowStyle({row}) {
      if (row.userId === 1) {
        return {
          backgroundColor: '#a0d8ef'
        }
      }
    },
    headClass() {  //表头居中显示
      return "text-align:center"
    },
    rowClass() { //表格数据居中显示
      return "text-align:center"
    },
    //请求管理员列表数据
    getAdminList() {
      this.$axios.get('admin/adminList', {params: this.queryInfo}).then(res => {
        // console.log(res.data.data.total == undefined)
        //如果没有搜索
        if (res.data.data.total !== undefined) {
          // console.log(res)
          if (res.data.status === 10000) {
            this.total = res.data.data.total;
            this.adminList = res.data.data.list;
            this.$message.success("请求管理员列表成功！")
          } else {
            this.$message.error("请求管理员列表失败！")
          }
        } else {
          //如果提交了单个adminId，就实现搜索功能
          if (res.data.status === 10000) {
            this.total = 1;
            this.adminList = [];
            this.adminList.push(res.data.data)
            this.$message.success("搜索管理员成功！");
          } else {
            this.$message.error("搜索管理员失败！")
          }
        }
      })
    },
    //当每页数据条数发生改变的额时候触发
    handleSizeChange(newSize) {
      this.queryInfo.pageSize = newSize;
      this.getAdminList();
    },
    //但每页数据条数发生改变的时候触发
    handleCurrentChange(newNum) {
      this.queryInfo.pageNum = newNum;
      this.getAdminList();
    },
    //修改当前管理员的角色
    changeStatus(row) {
      this.admin.userId = row.userId;
      this.admin.username = row.username;
      this.admin.password = null;
      this.admin.email = row.email;
      this.admin.mobile = row.mobile;
      this.admin.status = row.status;
      this.admin.createUserId = row.createUserId;
      this.admin.createTime = row.createTime;
      this.$axios.post('/admin/user/update', this.admin).then(res => {
        if (res.data.status === 10000) {
          this.$message.success("更新管理员成功！");
        } else {
          this.$message.error("更新管理员失败！");
        }
      })
    },
    //添加管理员
    addAdmin() {
      this.$refs["addAdminFormRef"].validate((valid) => {
        if (!valid) {
          return alert("请输入正确的信息！")
        } else {
          this.$axios.post('/user/add', this.admin).then(res => {
            //关闭对话框
            this.addAdminVisible = !this.addAdminVisible;
            this.$message.success("新增管理员成功！");
            //刷新列表
            this.getAdminList();
          })
        }
      })
    },
    //关闭添加管理员对话框
    closeAddAdminDialog() {
      //重置表单
      this.$refs["addAdminFormRef"].resetFields();
    },
    editAdmin(row) {
      //可以不用再发送一次请求
      this.admin = row;
      this.editAdminVisible = !this.editAdminVisible;
    },
    //确认更新
    updateAdmin() {
      this.$refs["editAdminFormRef"].validate((valid) => {
        if (!valid) {
          return alert("请输入正确的信息！")
        } else {
          this.$axios.post('/admin/user/update', this.admin).then(res => {
            if (res.data.status === 10000) {
              this.$message.success("修改管理员信息成功！");
              this.getAdminList();
              this.editAdminVisible = !this.editAdminVisible;
            } else {
              this.$message.error("修改管理员信息失败！");
              this.getAdminList();
              this.editAdminVisible = !this.editAdminVisible;
            }
          })
        }
      })
    },
    //关闭编辑管理员对话框
    closeEditAdminDialog() {
      //重置表单
      this.$refs["editAdminFormRef"].resetFields();
    },
    //删除管理员
    removeAdminItem(adminId) {
      this.$confirm('此操作将永久删除该用户, 是否继续?', '删除用户', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        //点击确定，发起删除用户
        this.$axios.post('/admin/user/delete/' + adminId).then(res => {
          this.$message.success("删除用户成功！")
          this.getAdminList();
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
    }
  },
  created() {
    this.getAdminList();
  }
}
</script>

<style scoped>

</style>
